import React from 'react';

const LogoAndSearchBarComponent = () => {
  return (
    <div className="main-slogo-wrapper">
      <div>
        <div className="main-logo-wrapper">
          <p className="main-word-slogo"> Derma </p>
          <p className="secondary-text secondary-text-slogo"> World </p>
        </div>
        <div className="second-line-logo-wrapper">
          <p className="secondary-text secondary-second-line-slogo"> By </p>
          <p className="black-color-small-size"> Leo Pharma </p>
        </div>
      </div>
      <div className="searchWrapper">
        <img
          className="search-icon"
          alt="search"
          src="data:image/svg+xml;utf8;base64,"
        />
        <input placeholder="SEARCH" className="search-bar" type="search" />
      </div>
    </div>
  );
};

export default LogoAndSearchBarComponent;
